import axios from "axios";

const instance = axios.create({
    // @ts-ignore
    baseURL: (window.location.hostname === 'localhost' && process.env.REACT_APP_DEV_SERVER) || (window.location.hostname !== 'localhost' && process.env.REACT_APP_PROD_SERVER),
    withCredentials: true,
})
console.log(process.env.REACT_APP_PROD_SERVER)
export const authApi = {
    inAuth(data?: inLoginType) {
        return instance.post('auth/me', data)
    },
}

export type inLoginType = {
    _id: string;
    email: string;
    name: string;
    avatar?: string;
    publicCardPacksCount: number;
    created: Date;
    updated: Date;
    isAdmin: boolean;
    verified: boolean;
    rememberMe: boolean;
    error?: string;
}